.capitalp-condition {
	font-size: 0.85em;
	margin: 1.8rem 0;
	padding: 1rem;
	background-color: #fcfcfc;
	color: #111;

	ul {
		margin-bottom: 0;
	}
}

.capitalp-series {
	&-container {
		margin-bottom: 3.6rem;
		text-align: center;
	}

	&-title {
		$size: 20px;
		padding-bottom: $size;
		margin-bottom: $size;
		position: relative;

		&:after {
			$width: 1.2em;
			content: "";
			display: inline-block;
			width: $width;
			height: 10px;
			border-bottom: 5px solid #111;
			position: absolute;
			bottom: 0;
			left: 50%;
			margin-left: $width / -2;
		}
	}

	&-desc {
		color: #444;
		font-size: 14px;
	}
}
